:root {
  --primary: "Poiret One";
  --secondary: "Amaranth";
  --third: "Poppins";

  --one: #57122e;
  --two: #af6e5c;
  --three: #bbbcb7;
  --four: #7ba5d5;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poiret One", "Segoe UI", sans-serif;
  font-family: var(--primary), "Segoe UI", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 10px;
  background: linear-gradient(#af6e5c, #57122e);
  background: linear-gradient(var(--two), var(--one));
}

/* ----------------------------- */
/* nav */
/* ----------------------------- */
nav {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
  background: #bbbcb7;
  background: var(--three);
  box-shadow: 0 1px 10px black;
  width: 100%;
}

nav img {
  margin-left: 0.5rem;
}

nav h2 {
  font-family: "Amaranth", cursive;
  font-family: var(--secondary), cursive;
  text-align: start;
  flex: 1 1;
  font-size: 2.25rem;
  padding: 0 1rem;
  margin: 0;
  color: #57122e;
  color: var(--one);
  text-shadow: 0.5px 2px 2px rgba(0, 0, 0, 0.75);
}

.nav-links {
  font-weight: 500;
  min-height: 10vh;
  position: relative;
  margin: 0;
  flex: 1 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-links a {
  height: 10vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #57122e;
  color: var(--one);
  text-align: center;
  outline: transparent;
  transition: all 0.2s ease;
}

.nav-links a:hover {
  background: #af6e5c;
  background: var(--two);
  color: #bbbcb7;
  color: var(--three);
}

.active li {
  outline: transparent;
  position: relative;
}

.active li::before {
  content: "";
  width: 100%;
  height: 0.1rem;
  position: absolute;
  top: -0.25rem;
  left: 0;
  background: #af6e5c;
  background: var(--two);
}

.active li::after {
  content: "";
  width: 100%;
  height: 0.1rem;
  position: absolute;
  bottom: -0.25rem;
  left: 0;
  color: white;
  background: #af6e5c;
  background: var(--two);
}

h1 {
  font-family: "Amaranth", cursive;
  font-family: var(--secondary), cursive;
}

/* ----------------------------- */
/* footer */
/* ----------------------------- */
footer {
  color: #bbbcb7;
  color: var(--three);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-contact {
  text-decoration: none;
  display: inline-block;
}

.footer-contact p {
  margin: auto;
  color: #bbbcb7;
  color: var(--three);
  font-size: 1.5rem;
  border: 2px solid #bbbcb7;
  border: 2px solid var(--three);
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  transition: all 0.2s ease;
}

.footer-contact p:hover {
  color: #7ba5d5;
  color: var(--four);
  border: 2px solid #7ba5d5;
  border: 2px solid var(--four);
  background: rgba(0, 0, 0, 0.5);
  transform: scale(1.1);
}

footer ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  margin-top: 2rem;
}

footer li {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 500;
  margin: 0 2rem;
  transition: all 0.2s ease;
}

footer li:hover {
  color: #7ba5d5;
  color: var(--four);
  transform: scale(1.1);
  cursor: not-allowed;
}

footer p {
  font-size: 0.75rem;
  text-align: center;
}

.hamburger {
  display: none;
}

.solo {
  text-align: center;
}

@media (max-width: 1000px) {
  nav {
    flex-direction: column;
  }
  nav img {
    position: absolute;
    top: 0;
    left: 0;
  }
  nav h2 {
    flex: 0 1;
    z-index: 2;
  }
  .mobile-open {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 0;
    margin: 2rem 0;
  }
  .mobile-close {
    display: none;
  }
  .nav-links {
    margin: 0;
    background: #af6e5c;
    background: var(--two);
    height: 100%;
    position: absolute;
    top: 0;
    z-index: 1;
  }
  .nav-links a {
    padding: 1rem 0;
  }
  .nav-links li {
    width: 100%;
    color: #bbbcb7;
    color: var(--three);
    font-size: 3rem;
  }
  .hamburger {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    color: #57122e;
    color: var(--one);
    height: 10vh;
    width: 10vw;
    z-index: 2;
  }
}

/*
